.paginatorWrapper {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1rem;
}
.pageElem {
  margin: 0.1em;
  color: darkslateblue;
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: bold;
}
.paginatorWrapper img {
  width: 0.7rem;
  margin: 0.1em;
}
.pageElem:hover {
  color: darkcyan;
}
.active {
  text-decoration: underline;
}
